
.rank__tag {
  background-color: #8000FF;
  color: #250060;
  display: inline-block;
  padding: 5px 10px;
}
.rank__title {
  display: inline-block;
  color: white;
  border-bottom: 4px solid #8000FF;
  margin: 5px 0 5px 0;
}
.rank__ship-wrapper {
  text-align: center;
  padding: 30px;
}
.rank__ship {
  max-width: 300px;
  margin: auto;
  animation: floating 1500ms ease-in-out infinite alternate-reverse;
}
@-webkit-keyframes floating {
0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
100% {
    -webkit-transform: translate3d(5%, -5%, 0);
            transform: translate3d(5%, -5%, 0);
}
}
@keyframes floating {
0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
100% {
    -webkit-transform: translate3d(5%, -5%, 0);
            transform: translate3d(5%, -5%, 0);
}
}
.rank__desc {
  border: 1px solid #FF0093;
  padding: 20px 30px;
  color: white;
}
.rank__desc-remark {
  font-size: 16px;
  font-weight: 700;
  color: #FF0093;
}
.skill-set {
  margin-bottom: 5px;
}
.skill-set--disabled .rank-img {
    opacity: 0.3;
}
.rank-wrapper {
  -ms-flex-preferred-size: 100px;
      flex-basis: 100px;
  margin-right: 10px;
}
.skill-set-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.rank-img {
  width: 100px;
  height: auto;
}
.skill-set__item {
  color: white;
  margin: 5px;
}
.skill-set__item--is-disabled {
    opacity: 0.3;
}
.skill-set__item--is-disabled .skill-set__icon-wrapper {
      cursor: not-allowed;
      background-color: rgba(255, 255, 255, 0.1);
}
.skill-set__item--is-selected .skill-set__icon-wrapper {
    border: 2px solid #FF0093;
    background-color: #6900D1;
}
.skill-set__item--is-done-require .skill-set__icon-wrapper {
    background-color: rgba(255, 255, 255, 0.1);
}
.skill-set__item--is-done-require .material-icons {
    color: #8000FF;
}
.skill-set__item--is-done-require .skill-set__number {
    color: #8000FF;
}
.skill-set__item--is-all-done .skill-set__icon-wrapper {
    position: relative;
}
.skill-set__item--is-all-done .skill-set__icon-wrapper:before {
      position: absolute;
      top: 0;
      right: 0;
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 16px 16px 0;
      border-color: transparent #8000FF transparent transparent;
}
.skill-set__icon-wrapper {
  cursor: pointer;
  width: 60px;
  height: 60px;
  background-color: #35006A;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.skill-set__icon-wrapper .material-icons {
    margin: auto;
}
.skill-set__number {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 3px;
  font-size: 14px;
}
.skill-set__number .material-icons {
    font-size: 14px;
    margin-right: 2px;
}
.skill-detail {
  border: 2px solid #FF0093;
}
.skill-detail__up {
    background-color: #8000FF;
    text-align: center;
    padding: 30px 0;
}
.skill-detail__up .material-icons {
      font-size: 100px;
}
.skill-detail__up .subtitle, .skill-detail__up .rank__title {
      margin: 30px 0 0 0;
}
.skill-detail__down {
    background-color: #6900D1;
    padding: 10px 0;
}
.skill-detail__down-wrapper {
      width: 70%;
      margin: auto;
}
.skill-detail__down .material-icons {
      margin-top: 20px;
}
.skill-detail__down .paragraph {
      margin: 0 0 10px 0;
}
.skill-detail__skill-btn {
  cursor: pointer;
  font-size: 16px;
  display: inline-block;
  outline: none;
  background-color: transparent;
  border: 2px solid white;
  color: white;
  text-align: center;
  width: 100%;
  padding: 10px 5px;
  margin-bottom: 10px;
  -webkit-transition: 200ms all ease-out;
  transition: 200ms all ease-out;
}
.skill-detail__skill-btn:hover {
    background-color: rgba(255, 0, 147, 0.6);
    border-color: transparent;
}
.skill-detail__skill-btn--is-selected {
    background-color: #FF0093 !important;
    border-color: transparent;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: 100ms all ease-out;
  transition: 100ms all ease-out;
}
body,
html {
